body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 880px;
  margin: 0 auto;
}

.App-header h1 {
  color: lightgray;
  text-align: center;
  margin: 20px;
}

.App-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.btn {
  background-color: lightskyblue;
  border: 1px solid lightblue;
  border-radius: 5px;
  color: white;
  display: block;
  font-size: 16px;
  font-weight: bold;
  margin: 10px 20px;
  padding: 10px 20px;
}

.container-btn {
  padding: 20px;
  text-align: center;
}

.container-btn button {
  margin: 0 auto;
}

textarea {
  margin: 20px 0;
  width: 100%;
}
.input-file {
  border: 1px solid lightgray;
  border-radius: 5px;
  margin: 20px;
  max-width: 250px;
  text-align: center;
}

.input-description {
  background-color: lightgray;
  margin-bottom: 20px;
  padding: 10px;
}

.input-file input {
  visibility: hidden;
  height: 0;
  width: 0;
}

.selected-file {
  display: inline-block;
  border: 1px solid lightgray;
  padding: 10px 20px;
  margin: 20px;
}

